
.yxfdgroup {
	margin-right: 5px;
	display: -webkit-inline-box;
	text-align: center;
}
.yxfdgrouptitle .yxfdgroup {
	color: #ff9800;
}
.labelsl {
	overflow: hidden;
	/*自动隐藏文字*/
	text-overflow: ellipsis;
	/*文字隐藏后添加省略号*/
	white-space: nowrap;
	/*强制不换行*/
	width: 150px;
	/*不允许出现半汉字截断*/
}
h2 {
	font-family: Candara, Calibri, Segoe, Segoe UI, Optima, Arial, sans-serif;
	color: #888;
}
.title span {
	font-size: 20px;
	font-weight: 400;
	color: #444;
}
/*导航按钮图标*/
.navbar-container .iconfont {
	font-size: 20px;
}
/*导航阴影*/
.navbar .navbar-container .bar.bar-header {
	box-shadow: 0 0 0px rgba(0, 0, 0, 0.15);
	border: 0px;
}
.bar.bar-header {
	box-shadow: 0 0 0px rgba(0, 0, 0, 0.15);
	border: 0px;
}
.rightitem .bar.bar-header .title {
	text-align: left;
}
/*图标*/
.chart {
	background: #fff;
	height: 330px;
}
/*图例*/
.legend {
	height: 65px;
	padding: 10px 20px 15px 10px;
	background: #fff;
}
.legendbox {
	height: 100%;
	border-radius: 30px;
	background: #f5f5f5;
	padding: 5px;
	display: -ms-flexbox;
	display: flex;
	display: -webkit-flex;
	/* Safari */
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
}
.legendbox .hitem {
	border-radius: 30px;
	width: 25%;
	height: 100%;
	line-height: 30px;
	text-align: center;
	color: #444;
}
.legendbox .hitem:active {
	background: #CCCCCC;
}
.legendbox .action {
	color: #FFFFFF;
	background: rgb(78, 78, 78);
}
.legendbox .bj {
	width: 8px;
	height: 8px;
	display: inline-block;
	margin-right: 5px;
}
/*主面板
.page-content{
	background: #fff;
}*/
.page.has-navbar .page-content {
	margin-bottom: 50px;
	/*padding-top: 100px;*/
}
.vipbox {
	-ms-flex-direction: column;
	    flex-direction: column;
	float: left;
	margin-left: 10px;
	line-height: 12px;
}
.vip {
	font-size: 30px;
	color: #fca902;
}
.b {
	background: #fff;
	height: 50px;
	width: 100%;
	position: fixed;
	bottom: 0;
	z-index: 10;
	line-height: 50px;
	text-align: center;
	color: #FFFFFF;
}
.b span {
	font-size: 16px;
}
.b .iconfont {
	margin-right: 2px;
}
.bleft {
	width: 40%;
	float: left;
	height: 100%;
	background: #27282a;
}
.bleft:active {
	color: #ccc;
}
.bright {
	width: 60%;
	float: left;
	height: 100%;
	background: #1f79f3;
}
.bright:active {
	color: #ccc;
}
/*侧边栏*/
/*.von-sidebar-wrapper .von-sidebar.open {
	width: 85% !important;
	background: #fff;
}*/
.rightBox .iconfont {
	margin-right: 5px;
	font-size: 18px;
}
.rightBox .b {
	position: absolute;
	border-top: 1px solid #1f79f3;
	box-sizing: border-box;
}
.rightBox .bleft {
	width: 50%;
	float: left;
	height: 100%;
	background: #fff;
	color: #444;
}
.rightBox .bleft:active {
	color: #ccc;
}
.rightBox .bright {
	width: 50%;
	float: left;
	height: 100%;
	background: #1f79f3;
}
.rightBox .bright:active {
	color: #ccc;
}
.rightBox {
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	position: fixed;
	z-index: 999999999999;
	/*background: rgba(0, 0, 0, 0.5);*/
}
.rightitem {
	width: 85%;
	height: 100%;
	background: #fff;
	float: right;
	box-shadow: 0 0 10px rgba(0, 0, 0, .2);
	-webkit-transition: -webkit-transform .4s cubic-bezier(.4, 0, 0, 1);
	box-shadow: 0 0 10px rgba(0, 0, 0, .2);
	box-sizing: border-box;
	position: relative;
	border-left: 1px solid #ddd;
}
.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
	will-change: transform;
	transition: all 250ms;
	height: 100%;
	top: 0;
	position: absolute;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	perspective: 1000;
}
.vux-pop-out-enter {
	opacity: 0;
	transform: translate3d(-100%, 0, 0);
}
.vux-pop-out-leave-active {
	opacity: 0;
	transform: translate3d(100%, 0, 0);
}
.vux-pop-in-enter {
	opacity: 0;
	transform: translate3d(100%, 0, 0);
}
.vux-pop-in-leave-active {
	opacity: 0;
	transform: translate3d(-100%, 0, 0);
}
