}*/
.rightBox .item {
	border: 1px solid #8abbfd;
	background: #ebf3ff;
	color: #1f79f3;
	font-size: 17px;
	padding-left: 10px;
}
.rightBox .list {
	padding-left: 0px;
}
.rightBox .list .icon {
	color: #1f79f3 !important;
}
.rightBox .bar-header {
	position: relative;
	height: 50px;
	background: #fff;
}
.rightBox .bar-header .title {
	line-height: 50px;
}
.sfq {
	/*height: 100px;*/
	padding: 0px 10px 0px 10px;
}
.sfqitem {
	height: 65px;
}
.sfqitem .legendbox {
	height: 35px;
}
.sfq .item-divider {
	/*padding-left: 10px;*/
	background: #fff;
	font-size: 14px;
	color: #444;
	line-height: 35px;
}
.sfq .item-divider span {
	float: right;
	color: #a1a1a1;
}
.sfq .item-divider .wxz {
	height: 35px;
	float: right;
	width: 100px;
}
.sfq .item-divider .wxz .hitem {
	width: 100%;
}
.sfq .item-divider:not(:first-child) {
	border-top: 1px solid #ddd;
}
.vue-slider .vue-slider-mark-label {
	color: #a1a1a1;
	font-size: 12px;
}
.vue-slider-process {
	background-color: #1f79f3;
}
.vue-slider-dot-tooltip-inner {
	border-color: #1f79f3;
	background-color: #1f79f3;
	font-size: 12px;
}
.vue-slider-marks .vue-slider-mark:first-child .vue-slider-mark-label {
	margin-left: 15px;
}
.vue-slider-marks .vue-slider-mark:last-child .vue-slider-mark-label {
	margin-left: -10px;
}
/*侧边栏选中按钮*/
.item-divider .legendbox,
.sfqitem .legendbox {
	background: #fff;
	width: 75%;
	float: right;
	font-weight: 300;
}
.item-divider .legendbox .hitem,
.sfqitem .legendbox .hitem {
	background: #f5f5f5;
	color: #a1a1a1;
	line-height: 25px;
	width: 30%;
}
.item-divider .legendbox .hitem:active,
.sfqitem .legendbox .hitem:active {
	background: #ccc;
}
.item-divider .legendbox .action,
.sfqitem .legendbox .action {
	background: #1f79f3;
	color: #fff;
}
.vue-slider-mark-step {
	width: 1px;
	height: 9px;
	top: -8px !important;
}
.loading-container {
	z-index: 999999999999999999999999999999;
}
.input .item {
	border: 1px solid #fff;
	background: #fff;
}
.navbar-container .button {
	font-weight: 700;
}



